.library {
  position: fixed;
  top: 0;
  left: 0;
  width: 22rem;
  height: 100%;
  background-color: white;
  box-shadow: 2px 2px 50px #808080a3;
  overflow: scroll;
  transform: translateX(-100%);
  transition: all 0.5s ease;
  opacity: 0;
  h2 {
    padding: 2rem;
  }
}

.librarySong {
  display: flex;
  padding: 1rem 2rem;
  cursor: pointer;
  transition: background 0.2s ease;
  img {
    width: 30%;
  }
  &:hover {
    background: lightblue;
  }
}

.songDescription {
  padding-left: 1rem;
  h3 {
    font-size: 1rem;
  }
  h4 {
    font-size: 0.8rem;
  }
}

* {
  scrollbar-width: thin;
  scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
}

*::-webkit-scrollbar {
  width: 5px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background: rgba(155, 155, 155, 0.5);
  border-radius: 20px;
  border: transparent;
}

.selected {
  background: violet;
}

.activeLibrary {
  transform: translateX(0%);
  opacity: 1;
}

@media screen and (max-width: 768px) {
  .library {
    width: 100%;
  }
}
